.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.ChatBox {
  text-align: left;
  margin: auto;
  width: 100%;
  height: 400px;
  max-width: 800px;
  border: 1px solid #555;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  position: relative;
}

.userInput {
  position:absolute;
  width: 100%;
}

.messageBox {
  /*margin-bottom: -30px;*/
  height: 100%;
  padding: 10px;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow-y: scroll;
  line-height: 1.3;
}

.txtInput {
  width: 80%;
  height: 30px;
  padding: 0 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.btnSend {
  height: 30px;
  width: 20%;
  border: 0;
  background-color: #222;
  color: white;
}

input [type="submit"] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

/* Modal */
.disableScreen {
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* end of modal */

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


